import React, {useState} from "react";
import s from "./Repositories.module.css"

type RenderSearchFieldType = {
    SearchQuery: string, //  
    setSearchQuery: (searchQuery:string)=>void //     
}

const RenderSearchField: React.FC<RenderSearchFieldType> = ({setSearchQuery, SearchQuery}) => {
    const [inputValue, setInputValue] = useState<string>( SearchQuery )
    const checkEnterPressed = (event: React.KeyboardEvent) => { //   Enter
        if (event.charCode===13) {
            setSearchQuery(inputValue)//    Enter
        }
    }
    return <div className={s.SearchFieldCommon}>
        <div>
            <input className={s.SearchField} type="text"
                   value={inputValue}
                   onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                       setInputValue( e.target.value )
                   }}
                   autoFocus //      
                   placeholder={"  gitHub "}//     
                   onKeyPress={checkEnterPressed} //   Enter
            /></div>

    </div>
}
export default RenderSearchField