import React from "react";
import {PaginationDataType} from "../../../redux/gh-list-reducer";
import s from "./Repositories.module.css"
import sc from "../../../common/classes/commonClasses.module.css"


type PaginationType = {
    totalRepositoriesCount: number,//
    pageSize: number,//
    currentPage: number,//   
    setPaginationData: (PaginationData: PaginationDataType) => void //      
    PaginationData: PaginationDataType
}
const Pagination: React.FC<PaginationType> = (
    {totalRepositoriesCount, pageSize, currentPage, setPaginationData, PaginationData}) => {

    // currentRange -  .   PortionSize  PagesCount
    //setCurrentRange -  currentRange    
    // PortionSizeLeft -    (  1)
    // const [mouseHovered, setMouseHovered] = useState("");
    const PagesCount = Math.ceil( totalRepositoriesCount / pageSize ); //     
    //    PageSize
    const pages = []; //      
    for (let i = 1; i <= PagesCount; i++) {
        //   
        pages.push( i ); //    
    }

    const renderSlicedPages = pages.map( (p) => { //   
        return (
            <div // 
                className={`${s.PIC} ${p===PaginationData.currentPage? s.PaginationItemCurrent:s.PaginationItem}`} //   
                key={p} //  - 
                onClick={() => { //  
                    setPaginationData( {...PaginationData, currentPage: p} )

                    //  onPageChanged( p );      
                }}
            >
                {p} {/*    */}
            </div>
        );
    } )

    return (
        <div className={sc.ToCenter}>  {/*  */}
            <div className={s.PaginationCommon}>{/* */}
                {renderSlicedPages} {/*    */}
            </div>
        </div>
    );
};

export default Pagination;
