import {InferActionsTypes} from "./store-redux";
import {CardDataType, ComThunkTp} from "../common/types/commonTypes";
import {AppActions} from "./app-reducer";
import {gitHubQuery} from "../api/graphQl";
import {GithubActions, initialStateGhList, MarkersListType} from "./gh-list-reducer";

const SET_CARD_DATA = "myApp/app-reducer/SET_CARD_DATA"; //   
const SET_CARD_MARKERS = "myApp/app-reducer/SET_CARD_MARKERS"; //   

export const GhCardActions = {
    setCardDataAC: (CardData: CardDataType) => { //     
        return {type: SET_CARD_DATA, CardData} as const
    },
    setCardMarkersAC: (CardMarkers: CardMarkersType) => { //     
        return {type: SET_CARD_MARKERS, CardMarkers} as const
    },

}

type CardActionTypes = InferActionsTypes<typeof GhCardActions> | InferActionsTypes<typeof AppActions>

type initialStateType = typeof initialState

let initialState = { //     
    CardData: //   
        {} as CardDataType,
    CardMarkers: { //    cards (    )
        IsCardDataUploaded: false //    
    }
}
export type CardMarkersType = typeof initialState.CardMarkers

let ghCardListReducer = (state: initialStateType = initialState, action: CardActionTypes): initialStateType => {//  
    let stateCopy: initialStateType; //      
    switch (action.type) {
        case SET_CARD_DATA: //      
            stateCopy = {
                ...state, //   
                CardData: action.CardData
            }
            return stateCopy; //     
        case SET_CARD_MARKERS: //    
            stateCopy = {
                ...state, //   
                CardMarkers: action.CardMarkers
            }
            return stateCopy; //     
        default:
            return state; //     
    }
}
export const getCardDataThCr = (cardId: string): ComThunkTp<CardActionTypes> => {//  SearchResultData  gitHub  axios/grapgQl
    return (dispatch, getState) => { // 

        dispatch( AppActions.setIsFetchingAC( true ) ) //   
        gitHubQuery.getCardData( cardId, getState().app.GITHUB_TOKEN ).then((response1: CardDataType)=>{
            console.log( "  SearchResultData  gitHub  axios/grapgQl", response1 )

            dispatch( GhCardActions.setCardDataAC( response1 ) )  //  SearchResultData  gitHub  store
            dispatch( AppActions.setIsFetchingAC( false ) ) //   

            dispatch(GhCardActions.setCardMarkersAC({
                ...getState().ghCard.CardMarkers,
                IsCardDataUploaded: true
            }))
            console.log( "      true" )

        })
    }
}

export default ghCardListReducer;










