import {GlobalStateType} from "../../../redux/store-redux";
import {connect} from "react-redux";
import React, {useEffect} from "react";
import CardCommon from "./CardCommon";
import {compose} from "redux";
import withRouter2 from "../../../common/hoc/withRouter2";
import {getCardDataThCr} from "../../../redux/gh-card-reducer";
import Preloader from "../../../common/Preloader/Preloader";
import NavigateToLoginHoc2 from "../../../common/hoc/NavigateToLoginHoc2";

const CardContainer:React.FC<mapStateToPropsType & mapDispatchToPropsType & OwnPropsType> = (
    {CardData, userId, getCardDataThCr, CardMarkers, IsFetching}) => {

    useEffect(()=>{
        console.log("      ")
        getCardDataThCr(userId) //       
    },[])

    return <div>
        {IsFetching && <Preloader/>} {/*  */}

        {/*          true,   */}
        {CardMarkers.IsCardDataUploaded && !IsFetching && <CardCommon CardData={CardData}/>}
    </div>
}

const mapStateToProps = (state: GlobalStateType) => {
    return {
        CardData: state.ghCard.CardData,//   
        CardMarkers: state.ghCard.CardMarkers, //    cards (    )
        IsFetching: state.app.IsFetching //  
    }
}
type OwnPropsType = {
    userId: string // id 
}
type mapStateToPropsType = ReturnType<typeof mapStateToProps>

type mapDispatchToPropsType = {
    getCardDataThCr: (cardId: string) => void //       userId

}
export default compose<React.ComponentType>(

    connect<mapStateToPropsType, //  mapStateToProps
        mapDispatchToPropsType, //  mapDispatchToProps
        OwnPropsType, //     
        GlobalStateType //    
        >( mapStateToProps, {
        getCardDataThCr
    } ),
    withRouter2,//   ID  URL     
    NavigateToLoginHoc2 //,   
)( CardContainer )
