import {InferActionsTypes} from "./store-redux";
import {apiCommon} from "../api/apiLocalStorage";
import {ComThunkTp, RepositoriesDataType} from "../common/types/commonTypes";
import {gitHubQuery} from "../api/graphQl";
import {AppActions} from "./app-reducer";

const SET_SEARCH_QUERY = "myApp/app-reducer/SET_SEARCH_QUERY"; //     
const SET_PAGINATION_DATA = "myApp/app-reducer/SET_PAGINATION_DATA"; //   
const SET_MY_REPOSITORIES_DATA = "myApp/app-reducer/SET_MY_REPOSITORIES_DATA"; //  MyRepositoriesData
const SET_SEARCH_RESULT_DATA = "myApp/app-reducer/SET_SEARCH_RESULT_DATA"; //  SearchResultData
const SET_REPOSITORIES_DATA = "myApp/app-reducer/SET_REPOSITORIES_DATA"; //  RepositoriesData
const SET_LIST_MARKERS = "myApp/app-reducer/SET_LIST_MARKERS"; //   

export const GithubActions = {
    setSearchQueryAC: (SearchQuery: string) => { //       
        return {type: SET_SEARCH_QUERY, SearchQuery} as const
    },
    setPaginationDataAC: (PaginationData: PaginationDataType) => { //         
        return {type: SET_PAGINATION_DATA, PaginationData} as const
    },
    setMyRepositoriesDataAC: (MyRepositoriesData: Array<RepositoriesDataType>) => { //    MyRepositoriesData
        return {type: SET_MY_REPOSITORIES_DATA, MyRepositoriesData} as const
    },
    setSearchResultDataAC: (SearchResultData: Array<RepositoriesDataType>) => { //    SearchResultData
        return {type: SET_SEARCH_RESULT_DATA, SearchResultData} as const
    },
    setRepositoriesDataAC: (RepositoriesData: Array<RepositoriesDataType>) => { //    MyRepositoriesData
        return {type: SET_REPOSITORIES_DATA, RepositoriesData} as const
    },
    setListMarkersAC: (ListMarkers: MarkersListType) => { //     
        return {type: SET_LIST_MARKERS, ListMarkers} as const
    },
}

type GithubActionTypes = InferActionsTypes<typeof GithubActions> | InferActionsTypes<typeof AppActions>

type initialStateGhListType = typeof initialStateGhList

export const initialStateGhList = { //    
    SearchQuery: "", //        
    PaginationData: { //  
        totalRepositoriesCount: 0, //   ,   
        pageSize: 10, //     
        currentPage: 1, //   
    },
    RepositoriesData: //     (  ,   )
        [] as Array<RepositoriesDataType>,
    ListMarkers: { //    List (    )
        IsRepositoriesDataUploaded: false, //     
    }
}
export type PaginationDataType = typeof initialStateGhList.PaginationData
export type MarkersListType = typeof initialStateGhList.ListMarkers

let ghListReducer = (state: initialStateGhListType = initialStateGhList, action: GithubActionTypes): initialStateGhListType => {//  
    let stateCopy: initialStateGhListType; //      
    switch (action.type) {
        case SET_SEARCH_QUERY: //      
            stateCopy = {
                ...state, //   
                SearchQuery: action.SearchQuery
            }
            return stateCopy; //     
        case SET_PAGINATION_DATA: //      
            stateCopy = {
                ...state, //   
                PaginationData: action.PaginationData
            }
            return stateCopy; //     
        case SET_REPOSITORIES_DATA: //   RepositoriesData
            stateCopy = {
                ...state, //   
                RepositoriesData: action.RepositoriesData,
            }
            return stateCopy; //     
        case SET_LIST_MARKERS: //   ListMarkers
            stateCopy = {
                ...state, //   
                ListMarkers: action.ListMarkers,
            }
            return stateCopy; //     
        default:
            return state; //     
    }
}

export const setPaginationDataThunkCreator = (PaginationData: PaginationDataType): ComThunkTp<GithubActionTypes> => {//  PaginationData  LocalStorage   
    return async (dispatch, getState) => { //   PaginationData  LocalStorage
        console.log( " PaginationData  LocalStorage" )
        const response1 = await apiCommon.putPaginationData( PaginationData )  //  PaginationData  localStorage
        if (response1) {
            console.log( " PaginationData  " )
            dispatch( GithubActions.setPaginationDataAC( response1 ) )  //   localStorage  PaginationData  store
        }
    }
}
export const getPaginationDataThunkCreator = (): ComThunkTp<GithubActionTypes> => {//  PaginationData  LocalStorage    
    return async (dispatch, getState) => { //   PaginationData  LocalStorage
        console.log( " PaginationData  LocalStorage" )
        const response1 = await apiCommon.getPaginationData()  //  PaginationData  localStorage
        if (response1) {
            console.log( " PaginationData  " )
            dispatch( GithubActions.setPaginationDataAC( response1 ) )  //   localStorage  PaginationData  store
        }
    }
}
export const setSearchQueryThunkCreator = (SearchQuery: string): ComThunkTp<GithubActionTypes> => {//  SearchQuery  LocalStorage   
    return async (dispatch, getState) => { //   SearchQuery  LocalStorage
        console.log( " SearchQuery  LocalStorage" )

        const response1 = await apiCommon.putSearchQuery( SearchQuery )  //  SearchQuery  localStorage
        console.log( " SearchQuery  " )
        dispatch( GithubActions.setSearchQueryAC( response1 ) )  //   localStorage  PaginationData  store

        dispatch( setPaginationDataThunkCreator( initialStateGhList.PaginationData ) ) //      

        if (response1 !== "") { //    
            console.log( "   SearchQuery  ,    graphQl  " )
            dispatch( getSearchResultDataThCr( SearchQuery ) )  //     SearchResultData
        }
    }
}
export const getSearchQueryThunkCreator = (): ComThunkTp<GithubActionTypes> => {//  SearchQuery  LocalStorage   
    return async (dispatch, getState) => { //   SearchQuery  LocalStorage
        console.log( " SearchQuery  LocalStorage" )

        const response1 = await apiCommon.getSearchQuery()  //  SearchQuery  localStorage
        console.log( " SearchQuery  " )
        dispatch( GithubActions.setSearchQueryAC( response1 ) )  //   localStorage  PaginationData  store
    }
}
export const getMyRepositoriesDataThCr = (): ComThunkTp<GithubActionTypes> => {//  MyRepositoriesData  gitHub  axios/grapgQl
    return (dispatch, getState) => { // 
        dispatch( AppActions.setIsFetchingAC( true ) ) //   
        gitHubQuery.getStarredRepos(getState().app.GITHUB_TOKEN).then( (response1: Array<RepositoriesDataType>) => {
            dispatch( GithubActions.setMyRepositoriesDataAC( response1 ) );  //  MyRepositoriesData  gitHub  store
            dispatch( GithubActions.setRepositoriesDataAC( response1 ) );  //  MyRepositoriesData  gitHub  store
            dispatch( AppActions.setIsFetchingAC( false ) ) //   

            dispatch( GithubActions.setListMarkersAC( {
                ...getState().ghList.ListMarkers,
                IsRepositoriesDataUploaded: true
            } ) )
            console.log( "getMyRepositoriesDataThCr - IsRepositoriesDataUploaded: true" )

            }
        )
    }
}
export const getSearchResultDataThCr = (SearchQuery: string): ComThunkTp<GithubActionTypes> => {//  SearchResultData  gitHub  axios/grapgQl
    return (dispatch, getState) => { // 
        dispatch( AppActions.setIsFetchingAC( true ) ) //   
        gitHubQuery.searchRepos( SearchQuery, getState().app.GITHUB_TOKEN ).then( (response1: Array<RepositoriesDataType>) => {
            console.log( "  SearchResultData  gitHub", response1 )
            dispatch( GithubActions.setSearchResultDataAC( response1 ) )  //  SearchResultData  gitHub  store
            dispatch( GithubActions.setRepositoriesDataAC( response1 ) );  //  SearchResultData  gitHub  store
            dispatch( AppActions.setIsFetchingAC( false ) ) //   

            dispatch( GithubActions.setListMarkersAC( {
                ...getState().ghList.ListMarkers,
                IsRepositoriesDataUploaded: true
            } ) )
            console.log( "  SearchResultData   true" )


        } )
    }
}


export default ghListReducer;










