import {initialStateGhList, PaginationDataType} from "../redux/gh-list-reducer";

export const apiCommon = { //    api   

  putPaginationData: (PaginationData:PaginationDataType) => { //  PaginationData  localStorage

    localStorage.setItem("PaginationData", JSON.stringify(PaginationData)); //  PaginationData  LocalStorage

    return apiCommon.getPaginationData()//  PaginationData  localStorage  
  },
  getPaginationData: () => { //  PaginationData  localStorage

    // @ts-ignore
    let Data1:PaginationDataType | null = JSON.parse(localStorage.getItem("PaginationData")); //  PaginationData  LocalStorage
    if (!Data1) {
      Data1=initialStateGhList.PaginationData //   PaginationData  
     // apiCommon.putPaginationData(Data1) //  PaginationData    localStorage   
    }
    return Data1 //  PaginationData  
  },
  putSearchQuery: (SearchQuery:string) => { //     localStorage

    localStorage.setItem("SearchQuery", JSON.stringify(SearchQuery)); //     LocalStorage

    return apiCommon.getSearchQuery()//  SearchQuery  localStorage  
  },
  getSearchQuery: () => { //  SearchQuery  localStorage

    // @ts-ignore
    let Data1:string = JSON.parse(localStorage.getItem("SearchQuery")); //  SearchQuery  LocalStorage
    if (!Data1) {
      Data1=initialStateGhList.SearchQuery //   SearchQuery  
     // apiCommon.putSearchQuery(Data1) //  SearchQuery    localStorage   
    }
    return Data1 //  SearchQuery  
  },

}
