import {InferActionsTypes} from "./store-redux";
import {ComThunkTp, RepositoriesDataType} from "../common/types/commonTypes";
import {
    getMyRepositoriesDataThCr,
    getPaginationDataThunkCreator,
    getSearchQueryThunkCreator,
    GithubActions
} from "./gh-list-reducer";
import {gitHubQuery} from "../api/graphQl";
import {Simulate} from "react-dom/test-utils";
import error = Simulate.error;

const SET_INITIALISED_APP = "myApp/app-reducer/SET_INITIALISED_APP"; //  
const SET_IS_FETCHING = "myApp/app-reducer/SET_IS_FETCHING"; //   
const SET_GITHUB_TOKEN = "myApp/app-reducer/SET_GITHUB_TOKEN"; //       
const SET_SERVER_ERROR = "myApp/app-reducer/SET_SERVER_ERROR"; //    

export const AppActions = {
    setInitialisedApp: () => { //     
        return {type: SET_INITIALISED_APP} as const
    },
    setIsFetchingAC: (IsFetching: boolean) => { //     
        return {type: SET_IS_FETCHING, IsFetching} as const
    },
    setGithubTokenAC: (GITHUB_TOKEN: string) => { //      GITHUB_TOKEN  
        return {type: SET_GITHUB_TOKEN, GITHUB_TOKEN} as const
    },
    setServerErrorAC: (ServerError: ServerErrorType) => { //        
        return {type: SET_SERVER_ERROR, ServerError} as const
    },

}

type AppActionTypes = InferActionsTypes<typeof AppActions>

type initialStateType = typeof initialState

const initialState = { //     
    initialisedApp: false, //   ?
    IsFetching: false, //   
    GITHUB_TOKEN: "", //  
    isAuth: false, //  ? (   )
    ServerError: "" //   
}

export type ServerErrorType = typeof initialState.ServerError //    

const appReducer = (state: initialStateType = initialState, action: AppActionTypes): initialStateType => {//  
    let stateCopy: initialStateType; //      
    switch (action.type) {
        case SET_INITIALISED_APP: //   
            stateCopy = {
                ...state, //   
                initialisedApp: true, //      true
            }
            return stateCopy; //     
        case SET_IS_FETCHING: //    
            stateCopy = {
                ...state, //   
                IsFetching: action.IsFetching
            }
            return stateCopy; //     
        case SET_GITHUB_TOKEN: //      
            stateCopy = {
                ...state, //   
                GITHUB_TOKEN: action.GITHUB_TOKEN,
                isAuth: true,
                ServerError: ""
            }
            return stateCopy; //     
        case SET_SERVER_ERROR: //     
            stateCopy = {
                ...state, //   
                ServerError: action.ServerError,
            }
            return stateCopy; //     
        default:
            return state; //     
    }
}

export const initialisedAppThunkCreator = (): ComThunkTp<AppActionTypes> => {//   
    return (dispatch, getState) => { //    
        const promise1 = dispatch( getPaginationDataThunkCreator() )//    
        const promise2 = dispatch( getSearchQueryThunkCreator() )//     
        Promise.all( [promise1, promise2] ) //    
            .then( () => {
                dispatch( AppActions.setInitialisedApp() ) //     true
            } )
    };
}

export const checkGhTokenThCr = (Token:string): ComThunkTp<AppActionTypes> => {//   github
    return (dispatch, getState) => { // 
        dispatch( AppActions.setIsFetchingAC( true ) ) //   
        gitHubQuery.checkGhToken(Token).then( (response1: any) => {
            dispatch( AppActions.setIsFetchingAC( false ) ) //   
            dispatch(AppActions.setGithubTokenAC(Token))
                console.log( "checkGhTokenThCr", response1 )
            }
        )
            .catch( (error1) => {
                dispatch( AppActions.setIsFetchingAC( false ) ) //   
                dispatch(AppActions.setServerErrorAC(error1.message))
                console.log( error1 )
                }
            )
    }
}


export default appReducer;










