import {PaginationDataType} from "../../../redux/gh-list-reducer";
import React from "react";
import s from "./Repositories.module.css";
import RepositoryItem from "./RepositoryItem";
import {RepositoriesDataType} from "../../../common/types/commonTypes";

type RenderRepositoriesType = {
    RepositoriesData: Array<RepositoriesDataType>, //      
    PaginationData: PaginationDataType //    
}
const RenderRepositories: React.FC<RenderRepositoriesType> = ({RepositoriesData, PaginationData}) => {
    console.log("RenderRepositories")
    const RepositoriesDataFiltered: Array<RepositoriesDataType>
        = RepositoriesData.filter((r, ind)=> //    
        ind>=PaginationData.pageSize*(PaginationData.currentPage-1) && //     
        ind<PaginationData.pageSize*PaginationData.currentPage //    pageSize 
    )

    return <div className={s.RenderRepositoriesCommon}>
        <div className={s.RepositoryItemHeaderCommon+ " " + s.PosAbs}>
            <div className={s.RepositoryItemNameHeader + " " + s.PosAbs}> </div>
            <div className={s.RepositoryItemStarsHeader + " " + s.PosAbs}></div>
            <div className={s.RepositoryItemLastCommitHeader+ " " + s.PosAbs} > </div>
            <div className={s.RepositoryItemLinkHeader+ " " + s.PosAbs}>  Github</div>
        </div>

        {RepositoriesDataFiltered.map((m, ind)=>{
            return <RepositoryItem
                key = {m.id} RepositoryName = {m.name} stars = {m.stargazers.totalCount} ind={ind}
                LastCommit={
                    m.defaultBranchRef &&
                    m.defaultBranchRef.target.committedDate}
                Link={m.url} id={m.id}
            />

        })}
    </div>
}
export default RenderRepositories