import React, {useEffect} from 'react';
import RepoListContainer from "./components/Github/RepositoriesList/RepoListContainer";
import ErrorBoundary from "./common/ErrorBoundary/ErrorBoundary";
import {GlobalStateType} from "./redux/store-redux";
import {connect} from "react-redux";
import {initialisedAppThunkCreator} from "./redux/app-reducer";
import Preloader from "./common/Preloader/Preloader";
import {HashRouter} from "react-router-dom";
import CardContainer from "./components/Github/Card/CardContainer";
import {Route, Routes} from "react-router-dom";
import InputCheckGhToken from "./components/Github/InputCheckGhToken/InputCheckGhToken";
import TokenContainer from "./components/Github/InputCheckGhToken/TokenContainer";

const App: React.FC<mapStateToPropsType & mapDispatchToPropsType> = (
    {initialisedAppThunkCreator, initialisedApp}) => {

    useEffect( () => {
        initialisedAppThunkCreator() //   

    }, [] )
    if (!initialisedApp) { //     
        return <Preloader/> //   
    }
    return <div>
        <HashRouter> {/*BrowserRouter  , HashRouter  gh-pages*/}
            <ErrorBoundary> {/*     */}
                <Routes> {/*   URL   */}
                    <Route path='' element={<TokenContainer/> }/> {/*   github */}
                    <Route path='/list/*' element={<RepoListContainer/>}/> {/*  - ,   */}
                    <Route path='/card/*' element={<CardContainer/>}/> {/* */}
                </Routes>
            </ErrorBoundary>
        </HashRouter>
    </div>
}

const mapStateToProps = (state: GlobalStateType) => {
    return {
        initialisedApp: state.app.initialisedApp, //   
    }
}
type mapStateToPropsType = ReturnType<typeof mapStateToProps>

type mapDispatchToPropsType = {
    initialisedAppThunkCreator: () => void,
}

export default connect<mapStateToPropsType,
    mapDispatchToPropsType,
    unknown,
    GlobalStateType>( mapStateToProps, {initialisedAppThunkCreator} )( App );
